.title {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0;
    height: 100vh;
    background: url('../../image/Title.PNG');
    background-size: cover;

    .filter {
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(22,22,22,0.8);
    }

    &-text { 
        z-index: 1;
        h2 {
            padding-right: 5px;

            color: white;
            font-size: 3vw;
            font-weight: 300;

            animation-name: cursor;
            animation-timing-function: step-end;
            animation-duration: 1.3s;
            animation-iteration-count: infinite;
            @keyframes cursor {
                0% { border-right: 2px solid white; };
                50% { border-right: 2px solid rgba(0,0,0,0); };
                100% { border-right: 2px solid white; };
            }
        }
    }
}

@media only screen and ( max-width: 1000px ) {
    .title {
        height: 40vh;
        .filter { height: 40vh }
        &-text h2 { font-size: 5vw }
    }
}